*{
  box-sizing: border-box;
}

/* FONTS */
.permanent-marker-regular {
  font-family: "Permanent Marker", cursive;
  font-weight: 400;
  font-style: normal;
}

.roboto-mono-secondary{
  font-family: "Roboto Mono", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
}
.jaro-secondary {
  font-family: "Jaro", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
/* END FONTS */

html, body {
  margin: 0;
  padding: 0;
  min-width: 375px;
  width:100%;
  font-family: "Micro 5", sans-serif;
  font-size: 16px;
  background-color:black;
}

body {
  display: flex;
  flex-direction: column;
}

header {
  position: fixed;
  top: 0;
  min-width: 300px;
  width:100%;
  margin: 0;

  /*Variable*/
  height:50px;

  display: flex;
  flex-direction: row;

  /*Variable*/
  padding: 10px;

  align-items: center;
  justify-content: space-between;

  

  border: 1px solid black;
  background-color: white;
  z-index: 2;
}

nav{
  justify-content: end;
}

header ul {
  list-style-type: none;
  display: inline-flex;
  align-items: center;
  padding: 0;
  margin: 0;
}


header a{
  text-decoration: none;
  color: black;
}

header h1 {
  /*Variable*/
  font-size: 1.75rem;
  
}

header li {
  padding-left: 10px;

  /*Variable*/
  font-size: 1rem;
}

.main{
  position: absolute;
  top: 50px;
  min-width: 300px;
  height: max-content;
  min-height: 90vh;
  width:100%;
  padding-bottom: 25px;
}

.feature {
  min-width: 300px;
  width:100%;
  padding: 50px;
  display: flex;
  text-align: center;
  background-image: url("res/backgrounds/stage_red_2.JPG");
  background-size: cover;
  background-repeat: no-repeat;
  background-position:center;
  background-color: black;
  height: 95vh;
  align-items: center;
}

.feature h2{
  margin: 0 auto;

  /*Variable*/
  font-size: 2.5rem;

  font-family: "Permanent Marker";


  color: white;
  margin-bottom: 100px;
  padding: 10px;
}

.shows{
  display: flex;
  flex-direction: column;
  min-width: 300px;
  width:100%;
  height: fit-content;
  justify-content: stretch;
  margin: 0 auto;
}

.table {
  width: 90%;

  /*variable*/
  min-width: 200px;

  text-align: left;
  padding: 15px 0px 15px 15px;
  margin: 0 0 50px 0;
  border-spacing: 1px;
}

.table-head{
  color: yellowgreen;

  font-family: "Permanent Marker";


  /*Variable*/
  font-size: 1.75rem;
}

#past_shows, #upcoming_shows, #section_about_us{
  display: flex;
  min-width: 300px;
  width: 100%;
  height: max-content;
  min-height: 90vh;

  color: white;
  flex-direction: column;
  text-align: center;

  align-items: center;
  justify-content: center;

 
}

#past_shows{
  background-image: url("res/backgrounds/stage_blurple.JPG");
  background-position:center;
  background-size: cover;
  background-repeat: no-repeat;
}

#upcoming_shows{
  background-image: url("res/backgrounds/stage_green.JPG");
  background-position:center;
  background-size: cover;
  background-repeat: no-repeat;
}


#past_shows h2, #upcoming_shows h2, table{
  background-color: black;
  background: rgba(8, 8, 8, 0.3);
  
}

#past_shows h2, #upcoming_shows h2{
  margin: 0 auto;
  padding: 5px;
  color: yellowgreen;

  font-family: "Permanent Marker";

  
  /*Variable*/
  font-size: 2.25rem;

  text-decoration: underline;

  
}

table thead th:nth-child(1) {
  width: 50%;
}

#past_shows table thead th:nth-child(2) , #upcoming_shows table thead th:nth-child(2) {
  width: 0%;
}

#past_shows table thead th:nth-child(3), #upcoming_shows table  thead th:nth-child(3) {
  width: 0%;
}

#past_shows table thead th:nth-child(4) #upcoming_shows table  thead th:nth-child(4) {
  width: 50%;
}

table td{

  /*Variable*/
  font-size: 1.25rem;
  vertical-align: top;
}

table a{
  text-decoration: underline;
  color: white;
}

.venue, .city{
  display: none;
}

#section_about_us{
  padding: 30px;
  background-image: url("res/backgrounds/merriweather_bg_1.JPG");
  background-position:center;
  background-size: cover;
  background-repeat: no-repeat;
}

#section_about_us img{
  /*Variable*/
  min-height: 300px;
  /*Variable*/
  min-width: 200px;

  margin: 0;

  border-radius: 10%;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

#section_about_us p{
  width: 90%;
  color: yellowgreen;
  padding: 15px;
  margin: 10px;
  text-align: center;

  /*Variable*/
  font-size: 1.5rem;

  font-family: "Permanent Marker";

  background: rgba(8, 8, 8, 0.8);

}

footer {
  position: fixed;
  height: 25px;
  min-width: 100%;
  z-index: 2;
  background-color:white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin: 30px 0 0 0;
  bottom: 0px;
}

.Gallery{
  display: flex;
  flex-direction: column;
  height: 80vh;
  width: 100%;
  padding-top: 50px;
  justify-content: center;
  align-items: center;

}

#backBtn{
  color: greenyellow;
  text-decoration: none;
  border: 1px solid yellowgreen;
  font-family: "Micro 5", sans-serif;
  font-size: 16px;
  background: none;
  margin: 25px 25px 100px 25px;
}

.image-slider {
  width: 80%;
  max-height: 70vh;
  text-align: center;
}
.image-slider .slick-dots {
  bottom: -30px;
}
.image-slider img, .image-slider video {
  max-width: 100%;
  max-height: 50vh;
  height: auto;
  margin: auto;
  
}

.slick-prev:before{
  color: black;
}

@media only screen and (orientation: landscape) {
  .image-slider {
    width: 50%;
  }
}

@media only screen and (min-width:500px)
{
  
  header h1 {
    /*Variable*/
    font-size: 2.15rem;
  }

  header li {
      padding-left: 5px;
      font-size: 1.25rem;
  }

  header li::before {   
    content: '';   
    display: inline-block;   
    height: 7px;   
    width: 7px;    
    background-size: 7px;   
    background-image: url('res/pick.png'); 
    background-repeat: no-repeat;   
    margin-right: 3px;
    background-position: center center;
    vertical-align: middle;
  }

  #past_shows, #upcoming_shows, #section_about_us{
    display: flex;
    min-width: 300px;
    width: 100%;
    height: max-content;
    min-height: 90vh;
  
    color: white;
    flex-direction: column;
    text-align: center;
  
    align-items: center;
    justify-content: center;
  
   
  }

  .feature h2{
      /*Variable*/
      font-size: 3.25rem;
  }

  .table-head{
      /* Variable */
      font-size: 2.25rem;
  }

  table td{
      /* Variable */
      font-size: 1.75rem;
  }

  #upcoming_shows h2, #past_shows h2,  #section_about_us h2{
      /*Variable*/
      font-size: 3.25rem;
  }
   
  #section_about_us img{
    /*Variable*/
    min-height: 400px;
    /*Variable*/
    min-width: 300px;
  
  }
  
  #section_about_us p{  
    /*Variable*/
    font-size: 2rem;
  }
}

@media only screen and (min-width:800px)
{

  main{
    padding-bottom: 35px;
  }

  header {
    /*Variable*/
    height:50px;
  
  
    border: 1px solid black;
    background-color: white;
    z-index: 2;
  }

  header h1 {
    /*Variable*/
    font-size: 3.5rem;
  }

  header li {
      padding-left: 15px;
      font-size: 1.75rem;
  }

  header li::before {   
    height: 15px;   
    width: 15px;    
    background-size: 15px;   
    margin-right: 5px;
  }


  table thead th:nth-child(1){
      width: 35%;
  }
  
  table thead th:nth-child(2) {
      width: 25%;
  }
  
  table thead th:nth-child(3) {
      width: 20%;
  }
  
  table thead th:nth-child(4) {
      width: 20%;
  }

  .venue, .city{
      display:table-cell;
  }
  
  #section_about_us img{
    /*Variable*/
    min-height: 500px;
    /*Variable*/
    min-width: 400px;
  
  }
  
  #section_about_us p{  
    /*Variable*/
    font-size: 2.25rem;
    max-width: 1000px;
  }

  footer {
    height: 35px;
    font-size: 2rem;
  }
  
}

@media only screen and (min-width:1300px)
{
  header h1 {
    /*Variable*/
    font-size: 4rem;
  }

  header li {
      padding-left: 15px;
      font-size: 2.25rem;
  }

  header li::before {   
    height: 15px;   
    width: 15px;    
    background-size: 15px;   
    margin-right: 5px;
  }
}